.mi-alert {
  position: relative;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 8px 16px;
  margin: 0;
  overflow: hidden;
  transition: opacity 0.2s;

  &__close {
    position: absolute;
    top: 12px;
    right: 16px;
    font-size: 12px;
    cursor: pointer;
  }
}
